import Vue from "vue";
import Vuex from "vuex";
import userStore from "@/store/modules/userStore.js";
import campStore from "@/store/modules/campStore.js";

Vue.use(Vuex);

const store = new Vuex.Store({
  modules: {
    userStore: userStore,
    campStore: campStore
  }
});

export default store;

